body{
    overflow: hidden;
}
.loading {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    height: 100vh;
    width: 100%;
    z-index: 999;
    background-color: #fff;
    transition: all .5s;
}
body.loaded .loading{
    opacity: 0;
    pointer-events: none;
}
body.loaded{
    overflow: auto;
} 
